  /* ======= Docs Section ======= */
  .docs {
    padding: 80px 0;
    background: #f5f5f5;
  }
  
  .docs .title {
    color: #074f66;
    margin-top: 0;
    margin-bottom: 30px;
  }
  
  .docs .docs-inner {
    max-width: 800px;
    background: #fff;
    padding: 30px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    margin: 0 auto;
  }
  
  .docs .block {
    margin-bottom: 60px;
  }
  
  .docs .code-block {
    margin: 30px inherit;
  }
  
  .docs .code-block pre[class*="language-"] {
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
  }
  
  /* ======= License Section ======= */
  .license {
    padding: 80px 0;
    background: #f5f5f5;
  }
  
  .license .title {
    margin-top: 0;
    margin-bottom: 30px;
    color: #074f66;
  }
  
  .license .license-inner {
    max-width: 800px;
    background: #fff;
    padding: 30px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    margin: 0 auto;
  }
  
  .license .info {
    max-width: 760px;
    margin: 0 auto;
  }
  
  .license .cta-container {
    max-width: 540px;
    margin: 0 auto;
    margin-top: 60px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
  }
  
  .license .cta-container .speech-bubble {
    background: #d6f3fc;
    color: #074f66;
    padding: 30px;
    margin-bottom: 30px;
    position: relative;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
  }
  
  .license .cta-container .speech-bubble:after {
    position: absolute;
    left: 50%;
    bottom: -10px;
    margin-left: -10px;
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid #d6f3fc;
  }
  
  .license .cta-container .icon-holder {
    margin-bottom: 15px;
  }
  
  .license .cta-container .icon-holder .svg-inline--fa {
    font-size: 56px;
  }
  
  .license .cta-container .intro {
    margin-bottom: 30px;
  }
  
  /* ======= Contact Section ======= */
  .contact {
    padding: 80px 0;
    background: #17baef;
    color: #fff;
  }
  
  .contact .contact-inner {
    max-width: 760px;
    margin: 0 auto;
  }
  
  .contact .title {
    color: #074f66;
    margin-top: 0;
    margin-bottom: 30px;
  }
  
  .contact .intro {
    margin-bottom: 60px;
  }
  
  .contact a {
    color: #074f66;
  }
  
  .contact a:hover {
    color: #042a36;
  }
  
  .contact .author-message {
    position: relative;
    margin-bottom: 60px;
  }
  
  .contact .author-message .profile {
    position: absolute;
    left: 30px;
    top: 15px;
    width: 100px;
    height: 100px;
  }
  
  .contact .author-message .profile img {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
  }
  
  .contact .author-message .speech-bubble {
    margin-left: 155px;
    background: #10b2e7;
    color: #074f66;
    padding: 30px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    border-radius: 4px;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    position: relative;
  }
  
  .contact .author-message .speech-bubble .sub-title {
    color: #074f66;
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 30px;
  }
  
  .contact .author-message .speech-bubble a {
    color: #fff;
  }
  
  .contact .author-message .speech-bubble:after {
    position: absolute;
    left: -10px;
    top: 60px;
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 10px solid #10b2e7;
  }
  
  .contact .author-message .speech-bubble .source {
    margin-top: 30px;
  }
  
  .contact .author-message .speech-bubble .source a {
    color: #074f66;
  }
  
  .contact .author-message .speech-bubble .source .title {
    color: #0c86ae;
  }
  
  .contact .info .sub-title {
    color: #0e98c5;
    margin-bottom: 30px;
    margin-top: 0;
  }
  
  .contact .social-icons {
    list-style: none;
    padding: 10px 0;
    margin-bottom: 0;
    display: inline-block;
    margin: 0 auto;
  }
  
  .contact .social-icons li {
    float: left;
  }
  
  .contact .social-icons li.last {
    margin-right: 0;
  }
  
  .contact .social-icons a {
    display: inline-block;
    background: #0c86ae;
    width: 48px;
    height: 48px;
    text-align: center;
    font-size: 24px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    margin-right: 8px;
    float: left;
  }
  
  .contact .social-icons a:hover {
    background: #e6ad14;
  }
  
  .contact .social-icons a .svg-inline--fa {
    color: #fff;
    text-align: center;
    margin-top: 8px;
  }
  
  /* ======= Footer ======= */
  .footer {
    padding: 15px 0;
    background: #042a36;
    color: #fff;
  }
  
  .footer .copyright {
    -webkit-opacity: 0.8;
    -moz-opacity: 0.8;
    opacity: 0.8;
  }
  
  .footer .fa-heart {
    color: #fb866a;
  }
  
  @media (max-width: 767px) {
    .header .main-nav .navbar-collapse {
      border-top: none;
      -webkit-box-shadow: none;
      -moz-box-shadow: none;
      box-shadow: none;
      width: 100%;
      left: 0;
      top: 60px;
      position: absolute;
      background: #fff;
    }
    .header .main-nav .navbar-collapse .navbar-nav {
      margin-left: 10px;
    }
    .header.navbar-fixed-top {
      height: 70px;
    }
    .start .btns .btn {
      margin-right: 0;
      clear: both;
      display: block;
      margin-bottom: 30px;
    }
    .start .title {
      font-size: 66px;
    }
    .start .meta {
      margin-top: 60px;
    }
    .start .meta li {
      float: none;
      display: block;
      margin-bottom: 5px;
    }
    .contact .author-message {
      text-align: center;
    }
    .contact .author-message .profile {
      position: static;
      margin: 0 auto;
      margin-bottom: 30px;
    }
    .contact .author-message .speech-bubble {
      margin-left: 0;
    }
    .contact .author-message .speech-bubble:after {
      display: none;
    }
    .contact .social-icons a {
      width: 36px;
      height: 36px;
      margin-right: 2px;
      font-size: 18px;
    }
    .contact .social-icons a .svg-inline--fa {
      margin-top: 7px;
    }
  }
  